<template>
  <div>

    <div style="margin: 10px 0">
      <el-input style="width: 200px " placeholder="请输入名称" suffix-icon="el-icon-search"
                v-model="username"></el-input>
      <el-input style="width: 200px " placeholder="请输入邮箱" suffix-icon="el-icon-message"
                v-model="email"></el-input>
      <el-input style="width: 200px " placeholder="请输入地址" suffix-icon="el-icon-position"
                v-model="address"></el-input>
      <el-button class="ml-5" type="primary" @click="load()">搜索</el-button>
      <el-button type="primary" @click="reset()">重置</el-button>
    </div>
    <div style="margin: 10px 0;">
      <el-button type="primary" @click="handleAdd()">新增<i class="el-icon-circle-plus-outline"
                                                          style="margin-left: 5px"></i></el-button>
      <el-button type="danger" @click="delbath()">批量删除<i class="el-icon-remove" style="margin-left: 5px"></i>
      </el-button>
      <el-upload
          class="upload-demo ml-10"
          :action="'http://' + serverIp + ':9090/user/import'" :show-file-list="false" accept=".xlsx" :on-success="handleExcelImportSuccess" style="display: inline-block;">
        <el-button type="primary" > 导入<i class="el-icon-bottom" style="margin-left: 5px"></i>
        </el-button>
      </el-upload>
      <el-button type="primary" @click="exp()" class="ml-10">导出<i class="el-icon-top" style="margin-left: 5px"></i></el-button>
    </div>
    <el-table :data="tableData" border stripe :header-cell-class-name="headerBg"
              @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column prop="username" label="用户名" width="140"></el-table-column>
      <el-table-column prop="role" label="角色" width="140"></el-table-column>
      <el-table-column prop="nickname" label="昵称" width="120"></el-table-column>
      <el-table-column prop="email" label="邮箱"></el-table-column>
      <el-table-column prop="phone" label="电话"></el-table-column>
      <el-table-column prop="address" label="地址"></el-table-column>
      <el-table-column label="操作" v-slot="scope" min-width="83px" style=" display:inline-block;" >
        <el-button type="success" @click="handleEdit(scope.row)">编辑<i class="el-icon-edit" style="margin-left: 3px"></i></el-button>
        <el-button type="danger" @click="handleDelete(scope.row.id)" style="margin-left: 0">删除<i class="el-icon-remove" style="margin-left: 3px"></i></el-button>
      </el-table-column>
    </el-table>
    <div style="padding: 10px 0">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[2, 5, 10, 20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </div>
    <el-dialog title="用户信息" :visible.sync="dialogFormVisible" width="30%">
      <el-form :model="form" label-width="80px" size="small">
        <el-form-item label="用户名">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色">
          <el-select clearable v-model="form.role" placeholder="请选择角色" style="width: 100%">
            <el-option
                v-for="item in roles"
                :key="item.name"
                :label="item.name"
                :value="item.flag">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="昵称">
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel()">取 消</el-button>
        <el-button type="primary" @click="save()">确 定</el-button>
      </div>

    </el-dialog>
  </div>
</template>

<script>
import {serverIp} from "../../public/config";

export default {
  name: "User",
  data() {
    return {
      serverIp: serverIp,
      tableData: [],
      total: 0,
      pageNum: 1,
      pageSize: 10,
      username: "",
      email: "",
      address: "",
      nickname: "",
      dialogFormVisible: false,
      form: {},
      multipleSelection: [],
      headerBg: 'headerBg',
      roles:[]
    }
  },
  created() {//页面创建之后
    //请求分页查询数据
    this.load()
  },
  methods: {
    load() {
      this.request.get("/user/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          username: this.username,
          address: this.address,
          email: this.email
        }
      }).then(res => {
        console.log(res)
        this.tableData = res.records
        this.total = res.total
      })


      this.request.get("/role").then(res =>{
        this.roles = res
      })
    },
    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load()
    },
    reset() {
      this.username = ""
      this.address = ""
      this.email = ""
      this.load()
    },
    handleAdd() {
      this.dialogFormVisible = true
      this.form = {}
    },
    save() {
      this.request.post("/user", this.form).then(res => {
        if (res) {
          this.$message.success("保存成功")
          this.dialogFormVisible = false
          this.load()
        } else {
          this.$message.success("保存失败")
        }
      })
    },
    handleEdit(row) {
      this.form = row
      console.log("this.form==>" + this.form)
      this.dialogFormVisible = true
    },
    handleCancel() {
      this.dialogFormVisible = false
      this.load()
    },
    handleDelete(id) {
      this.$confirm('此操作将永久删除该用户信息, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.request.delete("/user/" + id).then(res => {
          console.log("delRes==>" + res)
          this.load()
        })
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
    },
    handleSelectionChange(val) {
      console.log(val)
      this.multipleSelection = val
    },
    delbath() {
      this.$confirm('此操作将永久删除该用户信息, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let ids = this.multipleSelection.map(v => v.id)
        this.request.post("/user/del/batch", ids).then(res => {
          console.log("delRes==>" + res)
          if (res == true) {
            this.$message({
              type: 'success',
              message: '删除成功!',
            })
            this.load()
          } else {
            this.$message({
              type: 'info',
              message: '请至少选择一个用户进行删除'
            })
          }

        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },

    //导入用户信息成功提示
    handleExcelImportSuccess(){
      this.$message.success("用户信息上传成功!")
      this.load()
    },
    //导出用户信息
    exp() {
      window.open(`http://${serverIp}:9090/user/export`)
    },
  }
}

</script>
<style>
.headerBg {
  background-color: #eee !important;
}
</style>

